<template>
  <div class="header">
    <div class="appName" @click="toIndex">
      <p>PHPWord开发者文档-中文版</p>
      <p class="lt-version">Version 1.0</p>
    </div>
    <div class="cp">
   
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        options: [{
          value: 'Excel',
          label: 'PHPExcel开发文档'
        }],
        value: '',
      }
    }, methods: {
      toIndex() {
        this.$router.go({
          path: "/"
        })
      }
    }
  }
</script>

<style scoped>
  .header {
    width: 100%;
    height: 70px;
    background: #303643;
    position: relative;
  }

  .appName {
    font-size: 19px;
    padding-left: 25px;
    cursor: pointer;
    margin-top: 10px;
  }

  .appName > p {
    padding: 0;
    margin: 0;
    line-height: 1.5;
  }

  .appName, .cp {
    display: inline-block;
    color: white;
  }

  .appName > .lt-version {
    font-size: 14px;
    text-align: right;
  }

  .cp {
    position: absolute;
    right: 25px;
    line-height: 70px;
  }
</style>



