      // }
    }, methods: {}, watch: {
      '$route': function (to, from) {
        console.log("导航栏获取当前路由", this.$route.params.docs);
        sessionStorage.setItem("route", this.$route.params.docs);
        // this.routeParams = this.$route.params.docs;
        //    this.getData();
      }
    }, beforeMount() {
      if (sessionStorage.getItem('route') != null) {
        this.route_name = "/view/" + sessionStorage.getItem('route');
      }
    }
  }
</script>
